import React from 'react';
import FooterFirstStep from './FooterFirstStep';
import * as Consts from '../../shared/Consts';

export default class ProfileStep5 extends React.Component {
  render() {
    const { CurrentTab } = this.props;
    if (CurrentTab !== Consts.STEP_5) {
      return null;
    }

    return (
      <div className="steppers">
        <div className="step_title">Placeholder</div>
        <div className="step_texts">Placeholder</div>
        <FooterFirstStep
          SaveHandler={() => {
            this.props.SaveHandler();
          }}
          SaveNextHandler={() => {
            this.props.GoToStepHandler(Consts.STEP_6);
          }}
        />
      </div>
    );
  }
}
