import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import InputerForm from '../Shared/InputerForm';
import * as FieldDef from '../../shared/FieldDefinition/BuyerProfile';
import * as InputHelper from '../Shared/InputHelper';
import * as InputValuesHelper from '../../shared/InputValuesHelper';
import * as Consts from '../../shared/Consts';

export default class OfferVerifications extends React.Component {
  render() {
    const { InputValues, CurrentTab } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FieldDefinitions,
    };

    if (CurrentTab !== Consts.BuyerOfferVerifications) {
      return null;
    }

    return (
      <>
        <div className="main_form_info steppers">
          <div className="title2">Verifications</div>
          <div className="b_texts">
            We need to collect some information to prepare offer documents and
            to share with our transaction partners. This will be the last step
            before submitting an offer for verification!
          </div>
          <div className="pro_tab">
            <div className="tab1">Financial Verifications</div>
            <div className="tab_divider desktop" />
            <div className="tab2 desktop">
              Purchasing property requires verification of financing commitment
              as well as the verification of funds required to close.
            </div>
          </div>
          <div
            className="form_inputs greyer mobile"
            style={{ marginLeft: '0', marginBottom: '20px' }}
          >
            <span style={{ color: '#111' }}>Note:</span> Purchasing property
            requires verification of financing commitment as well as the
            verification of funds required to close.
          </div>

          <div
            className="form_inputs"
            id="set5"
            style={{ marginBottom: '50px' }}
          >
            <div className="required required-options">
              <div className="t_texts">
                Will you be obtaining a loan in connection with this purchase ?
              </div>
              {InputHelper.GenerateInput({
                ...FieldProps,
                FieldName: 'buyer_info_financial_verifications_need_loan',
              })}
            </div>
          </div>

          {InputValuesHelper.GetInputValue(
            InputValues,
            'buyer_info_financial_verifications_need_loan',
          ) === Consts.OPTION_YES_VALUE && (
            <div className="reveals animated_fastest" id="info5">
              <div className="pro_tab alternatess">
                <div className="tab1">Pre-Approval</div>
              </div>
              <div className="form_inputs" style={{ marginBottom: '50px' }}>
                <div id="set6">
                  <div className="required required-options">
                    <div className="t_texts">
                      Have you been pre-approved for a loan?
                    </div>
                    {InputHelper.GenerateInput({
                      ...FieldProps,
                      FieldName:
                        'buyer_info_financial_verifications_pre_approved_loan_active',
                    })}
                  </div>
                </div>

                {InputValuesHelper.GetInputValue(
                  InputValues,
                  'buyer_info_financial_verifications_pre_approved_loan_active',
                ) === Consts.OPTION_YES_VALUE && (
                  <>
                    <div className="inputer_form halfs">
                      <div className="b_texts">
                        {InputHelper.GetFieldDescription({
                          ...FieldProps,
                          FieldName:
                            'buyer_info_financial_verifications_pre_approved_down_payment_amount',
                        })}
                      </div>
                      {InputHelper.GenerateInput({
                        ...FieldProps,
                        FieldName:
                          'buyer_info_financial_verifications_pre_approved_down_payment_amount',
                      })}
                    </div>
                    <div
                      className="reveals animated_fastest"
                      id="info7"
                      style={{ marginTop: '30px' }}
                    >
                      <div className="pro_tab alternatess">
                        <div className="tab1">Documentation</div>
                        <div className="tab_divider desktop" />
                        <div className="tab2 desktop">
                          Please upload your loan pre-approval as well as any
                          verification of liquid assets that will be used for
                          your down payment.
                        </div>
                      </div>
                      <div
                        className="form_inputs greyer mobile"
                        style={{ marginLeft: '0', marginBottom: '20px' }}
                      >
                        <span style={{ color: '#111' }}>Note:</span> Please
                        upload your loan pre-approval as well as any
                        verification of liquid assets that will be used for your
                        down payment.
                      </div>
                      <div className="form_inputs">
                        <div className="t_texts">Pre-Approval Letter</div>
                        {InputHelper.GenerateInput({
                          ...FieldProps,
                          FieldName:
                            'buyer_info_financial_verifications_pre_approval_letter',
                          Message:
                            'Account Statement | Investment, Checking, Savings',
                        })}
                        <div className="t_texts">
                          Down Payment Verification of Funds
                        </div>
                        {InputHelper.GenerateInput({
                          ...FieldProps,
                          FieldName:
                            'buyer_info_financial_verifications_down_payment',
                          Message:
                            'Account Statement | Investment, Checking, Savings',
                        })}
                      </div>
                    </div>
                  </>
                )}

                {InputValuesHelper.GetInputValue(
                  InputValues,
                  'buyer_info_financial_verifications_pre_approved_loan_active',
                ) === Consts.OPTION_NO_VALUE && (
                  <>
                    <div className="reveals animated_fastest" id="info6">
                      <div
                        className="b_texts alternates2"
                        style={{ marginBottom: '30px' }}
                      >
                        Loan pre-approval is a vital step in the purchase
                        process. You will need to obtain this approval from a
                        financial institution before you are eligible to
                        initiate a purchase offer.
                      </div>
                      <div className="form_inputs">
                        <div className="required required-options">
                          <div className="t_texts">
                            Would you like us to connect you with one of our top
                            rated lenders?
                          </div>
                          {InputHelper.GenerateInput({
                            ...FieldProps,
                            FieldName:
                              'buyer_info_financial_verifications_connect_to_lender_active',
                          })}
                        </div>
                      </div>
                    </div>

                    {InputValuesHelper.GetInputValue(
                      InputValues,
                      'buyer_info_financial_verifications_connect_to_lender_active',
                    ) === Consts.OPTION_YES_VALUE && (
                      <div
                        className="b_texts alternates2"
                        style={{ marginBottom: '30px' }}
                      >
                        {`Great! We'll put you in touch with a qualified lender.`}{' '}
                      </div>
                    )}
                  </>
                )}
              </div>
            </div>
          )}

          {InputValuesHelper.GetInputValue(
            InputValues,
            'buyer_info_financial_verifications_need_loan',
          ) === Consts.OPTION_NO_VALUE && (
            <div className="reveals animated_fastest" id="info8">
              <div
                className="pro_tab alternatess"
                style={{ marginBottom: '20px' }}
              >
                <div className="tab1">Verification of Funds</div>
                <div className="tab_divider" />
                <div className="tab3">All Cash Purchase</div>
              </div>
              <div className="b_texts">
                Please upload documentation of funds equal to or greater than
                the amount of your offer price. This information will be
                reviewed by our finance team and presented to the seller with
                your offer.
              </div>
              <div className="form_inputs">
                {InputHelper.GenerateInput({
                  ...FieldProps,
                  FieldName: 'buyer_info_financial_verifications_of_funds',
                  Message: 'Account Statement | Investment, Checking, Savings',
                })}
              </div>
            </div>
          )}

          <div className="reveals animated_fastest">
            <div className="pro_tab alternatess">
              <div className="tab1">Purchase Disclosures</div>
            </div>
            <div className="form_inputs" style={{ marginBottom: '50px' }}>
              <div className="required required-options">
                <InputerForm
                  {...FieldProps}
                  FieldName="buyer_info_financial_verifications_checked_disclosure"
                />
              </div>
              <div className="required required-options">
                <InputerForm
                  {...FieldProps}
                  FieldName="buyer_info_financial_verifications_checked_terms"
                />
              </div>
              <div className="required required-options">
                <InputerForm
                  {...FieldProps}
                  FieldName="buyer_info_financial_verifications_checked_buyer_advisory"
                />
              </div>
            </div>
          </div>
        </div>
        <FooterMiddleStep
          SaveHandler={() => {
            this.props.SaveHandler(DataSections);
          }}
          HideSaveButton={this.props.ReadOnly === true}
          PreviousStepHandler={() => {
            this.props.GoToStepHandler(Consts.BuyerOfferDetails);
          }}
          SaveNextHandler={() => {
            if (this.props.ReadOnly === true) {
              this.props.GoToStepHandler(Consts.BuyerOfferSubmission);
            } else {
              this.props.SaveNextHandler(
                Consts.BuyerOfferSubmission,
                DataSections,
              );
            }
          }}
          NextButtonText={
            this.props.ReadOnly === true ? 'Next' : 'Submit Offer'
          }
        />
        {this.props.ReadOnly !== true && (
          <div className="footer_alert">
            <div className="item">
              <div className="c_text">
                Submitting offer will send it to NILE advisor for review. This
                process takes about 12 hours. Once everything has been verified,
                you will receive the final offer letter for your approval and
                signature. After you sign this final offer package and return it
                back to the NILE advisor, we will submit it to the seller for
                review and approval.
              </div>
            </div>
          </div>
        )}
      </>
    );
  }
}

const DataSections = [Consts.DATA_SECTION_BUYER_FINANCIAL_VERIFICATIONS];
