import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import * as FieldDef from '../../shared/FieldDefinition/BuyerProfile';
import * as InputHelper from '../Shared/InputHelper';
import * as Consts from '../../shared/Consts';

export default class ProfileStep8 extends React.Component {
  render() {
    const { CurrentTab } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.FieldDefinitions,
    };

    if (CurrentTab !== Consts.STEP_8) {
      return null;
    }

    return (
      <div className="main_form_info steppers">
        <div className="title2">
          <div className="number_title">08</div> Statement of Information{' '}
        </div>
        <div className="b_texts" style={{ maxWidth: 'none', width: '100%' }}>
          Nile is required to collect certain personal information to satisfy
          legal, tax, and underwriting requirements in connection with the
          purchase or sale of real estate. The collected information serves to
          establish identity, eliminate matters affecting persons of similar
          name, protect you against forgeries, and speed the completion of your
          title and escrow services. Nile technology maintains procedural
          safeguards that comply with federal standards to protect the
          confidentiality and security of non-public personal information.{' '}
        </div>
        <div className="form_inputs another2">
          <div className="inputer_form halfs">
            {InputHelper.GenerateInput({
              ...FieldProps,
              FieldName: 'buyer_info_statement_social_security_number',
            })}
          </div>
          <div className="inputer_form halfs">
            {InputHelper.GenerateInput({
              ...FieldProps,
              FieldName: 'buyer_info_statement_drivers_license_number',
            })}
          </div>
          <br />
          <div className="inputer_form thirds">
            {InputHelper.GenerateInput({
              ...FieldProps,
              FieldName: 'buyer_info_statement_state_of_issuance',
            })}
          </div>
          <div className="inputer_form thirds">
            {InputHelper.GenerateInput({
              ...FieldProps,
              FieldName: 'buyer_info_statement_date_of_birth',
            })}
          </div>
        </div>
        <FooterMiddleStep
          SaveHandler={() => {
            this.props.SaveHandler(DataSections);
          }}
          PreviousStepHandler={() => {
            this.props.GoToStepHandler(Consts.STEP_7);
          }}
          SaveNextHandler={() => {
            this.props.SaveNextHandler(Consts.STEP_9, DataSections);
          }}
        />
      </div>
    );
  }
}

const DataSections = [Consts.DATA_SECTION_BUYER_STATEMENT_OF_INFORMATION];
