import React from 'react';
import FooterFirstStep from './FooterFirstStep';
import * as Consts from '../../shared/Consts';
import { ContactUsForm } from '../ContactUs/ContactUsForm';

export default function OfferWelcome(props) {
  const { CurrentTab } = props;
  if (CurrentTab !== Consts.BuyerOfferWelcome) {
    return null;
  }

  return (
    <div className="steppers">
      <div className="step_title">Start the Journey</div>
      <div
        className="step_texts"
        style={{ maxWidth: '80%', textAlign: 'left', color: '#000' }}
      >
        <div>
          {`To bring you an easy and streamlined buying experience, we'll start by
          gathering some information so you're ready to place an offer on your
          dream property.`}
        </div>
        <br />
        <div>This platform will help guide you through: </div>
        <div>
          <strong> Important buyer information</strong>{' '}
        </div>
        <div>
          <strong> Offer confirmation process and verification</strong>
        </div>
        <div>
          <strong> Verification of funds and supporting documentation</strong>{' '}
        </div>
        <div>&nbsp;</div>
        <div>
          You can save your progress at any time and, if you have any questions,
          do not hesitate to{' '}
          <ContactUsForm handleSubmit={props.SubmitContactUsHandler}>
            <span className="blue_text">reach out </span>
          </ContactUsForm>
          to us at{' '}
          <a href="mailto:hello@nilehome.com" className="blue_text">
            hello@nilehome.com
          </a>{' '}
          or to your represented NILE Advisor.
        </div>
        <br />
        <div>Lets Dream Forward Together.</div>
        <div>&nbsp;</div>
        <div>NILE</div>
      </div>
      <FooterFirstStep
        SaveHandler={() => {
          props.SaveHandler();
        }}
        SaveNextHandler={() => {
          props.GoToStepHandler(Consts.BuyerOfferBuyerInfo);
        }}
      />
    </div>
  );
}
