import React from 'react';
import inspectionIcon1 from '../../images/inspection_icon1.svg';
import inspectionIcon2 from '../../images/inspection_icon2.svg';
import inspectionIcon3 from '../../images/inspection_icon3.svg';
import inspectionIcon4 from '../../images/inspection_icon4.svg';

export default class Inspections extends React.Component {
  render() {
    const { ValueChangeHandler, InputValues } = this.props;

    var planDescription = '';

    const inspectionPlanFieldName = 'buyer_info_inspection_plan';
    const inspectionPlan1 = 'basic';
    const inspectionPlan2 = 'recommended';
    const inspectionPlan3 = 'choose_own';
    const inspectionPlan4 = 'no_inspection';

    var inspectionPlan =
      InputValues[inspectionPlanFieldName] === undefined
        ? ''
        : InputValues[inspectionPlanFieldName];

    var checkedPlan1 = false;
    if (inspectionPlan === inspectionPlan1) {
      checkedPlan1 = true;
      planDescription = 'Basic plan, description(TBD)';
    }

    var checkedPlan2 = false;
    if (inspectionPlan === inspectionPlan2) {
      checkedPlan2 = true;
      planDescription = 'Recomended plan, description(TBD)';
    }

    var checkedPlan3 = false;
    if (inspectionPlan === inspectionPlan3) {
      checkedPlan3 = true;
      planDescription = 'Choose you own, description(TBD)';
    }
    var checkedPlan4 = false;
    if (inspectionPlan === inspectionPlan4) {
      checkedPlan4 = true;
      planDescription = 'No inspection, description(TBD)';
    }

    const itemplanDescriptionStyle = {
      position: 'absolute',
      top: '200px',
      width: '100%',
      fontSize: '16px',
      paddingTop: '15px',
      color: '#808080',
      fontFamily: 'AvenirMedium',
    };

    return (
      <>
        <div className="inspections">
          <div className="inspects">
            <img src={inspectionIcon4} width="100%" alt="Inspection" />
            <input
              type="radio"
              id="inspection_d"
              value={inspectionPlan4}
              name={inspectionPlanFieldName}
              onChange={ValueChangeHandler}
              checked={checkedPlan4}
            />
            <label htmlFor="inspection_d">
              No
              <br />
              Inspection
            </label>
            <div style={itemplanDescriptionStyle}>$0</div>
          </div>
          <div className="inspects">
            <img src={inspectionIcon1} width="100%" alt="Inspection" />
            <input
              type="radio"
              id="inspection_a"
              value={inspectionPlan1}
              name={inspectionPlanFieldName}
              onChange={ValueChangeHandler}
              checked={checkedPlan1}
            />
            <label htmlFor="inspection_a">Basic Home Inspection</label>
            <div style={itemplanDescriptionStyle}>$500</div>
          </div>

          <div className="inspects">
            <img src={inspectionIcon2} width="100%" alt="Inspection" />
            <input
              type="radio"
              id="inspection_b"
              value={inspectionPlan2}
              name={inspectionPlanFieldName}
              onChange={ValueChangeHandler}
              checked={checkedPlan2}
            />
            <label htmlFor="inspection_b">
              Recommended
              <br />
              Inspection
            </label>
            <div style={itemplanDescriptionStyle}>$1,500</div>
          </div>

          <div className="inspects">
            <img src={inspectionIcon3} width="100%" alt="Inspection" />
            <input
              type="radio"
              id="inspection_c"
              value={inspectionPlan3}
              name={inspectionPlanFieldName}
              onChange={ValueChangeHandler}
              checked={checkedPlan3}
            />
            <label htmlFor="inspection_c">Choose Your Own </label>
          </div>
        </div>
        <div
          className="b_texts"
          style={{ maxWidth: '550px', margin: 'auto', marginBottom: '25px' }}
        >
          {planDescription}
        </div>
      </>
    );
  }
}
